//variables
$duration: 1.5s
$container-size: 250px
$box-size: 33px
$box-border-radius: 15%

.containerLoader
  width: $container-size
  display: flex
  justify-content: space-between
  align-items: center
  position: relative
  margin: 15% auto

  .containerLoader-element
    width: $box-size
    height: $box-size
    position: relative
    display: block
    transform-origin: -50% center
    border-radius: $box-border-radius

    &:after
      content: ''
      width: 100%
      height: 100%
      position: absolute
      top: 0
      right: 0
      background-color: lightblue
      border-radius: $box-border-radius
      box-shadow: 0 0 10px 0 rgba(#1C9FFF, 0.4)

    &:nth-child(1)
      animation: slide $duration ease-in-out infinite alternate
    &:after
      animation: color-change $duration ease-in-out infinite alternate

    @for $i from 1 to 5
      &:nth-child(#{$i + 1})
        animation: flip-#{$i} $duration ease-in-out infinite alternate
      &:after
        animation: squidge-#{$i} $duration ease-in-out infinite alternate

      &:nth-child(2):after
        background-color: #1C9FFF
      &:nth-child(3):after
        background-color: #1FB1FD
      &:nth-child(4):after
        background-color: #22C7FB
      &:nth-child(5):after
        background-color: #23D3FB

    @keyframes slide
      0%
        background-color: #1795FF
        transform: translatex(0vw)
      100%
        background-color: #23D3FB
        transform: translatex(calc(#{$container-size} - (#{$box-size}  * 1.25)))

    @keyframes color-change
      0%
        background-color: #1795FF
      100%
        background-color: #23D3FB

    @for $i from 1 to 5

      @keyframes flip-#{$i}
        0%
        #{$i * 15}%
          transform: rotate(0)
        #{$i * 15 + 20}%
        100%
          transform: rotate(-180deg)

      @keyframes squidge-#{$i}
        #{$i * 15 - 10}%
          transform-origin: center bottom
          transform: scalex(1) scaley(1)
        #{$i * 15}%
          transform-origin: center bottom
          transform: scalex(1.3) scaley(0.7)
        #{$i * 15 + 10}%
        #{$i * 15 + 5}%
          transform-origin: center bottom
          transform: scalex(0.8) scaley(1.4)
        #{$i * 15 + 40}%
        100%
          transform-origin: center top
          transform: scalex(1) scaley(1)
        #{$i * 15 + 25}%
          transform-origin: center top
          transform: scalex(1.3) scaley(0.7)
